<template>
	<view>
		<view class="user">
			<view class="user-info">
				<image :src="avatarUrl" mode=""></image>
				<view class="user-name">{{nickName}}</view>
			</view>
		</view>
		<view class="list-info">
			<view class="list-item">
				<navigator class="list">
					<view class="list-name">
						<view class="iconfont icon-round_people_fill grey"></view>
						<text>个人资料</text>
					</view>
					<view class="iconfont icon-youjiantou"></view>
				</navigator>
				<view class="list" @tap="logout()">
					<view class="list-name">
						<view class="iconfont icon-round_people_fill grey"></view>
						<text>退出登录</text>
					</view>
					<view class="iconfont icon-youjiantou"></view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				nickName:uni.getStorageSync('nickName'),
				avatarUrl: uni.getStorageSync('avatarUrl')
			}
		},
		methods: {
			logout(){
				uni.showModal({
					title: '是否退出登录',
					success:(res) => {
						if (res.confirm){
							try {
								uni.clearStorageSync()
								uni.reLaunch({
									url: '../login/login'
								})
							} catch (e) {
								
							}
						}else if (res.cancel){
							console.log('用户点击取消');
						}
					}
				})
				
			}
		}
	}
</script>

<style lang="less">
	@import url("my.less");
</style>
